<template>
  <div class="box">
    <div class="header-box">
      <img src="/static/imgs/temp/图层 10@2x.png" alt="" />
    </div>
    <div class="detail-box user-manage-wrap">
      <el-form :model="userInfo" :rules="rules" label-position="left">
        <el-form-item label="用户名称" :label-width="labelWidth" prop="name">
          <el-col :span="16">
            <el-input :class="{'editState': editState.nameState == false}" v-model="userInfo.userName" :placeholder="editState.nameState ? '未填写' : '最多20字'" :disabled="editState.nameState"></el-input>
            <el-button v-show="editState.nameState" type="text" @click.prevent="editState.nameState = false">修改</el-button>
            <el-button v-show="!editState.nameState" size="mini" @click.prevent="edituserName">确定</el-button>
          </el-col>
          <el-col :span="8">
            <div class="el-form_item-tip">用户名称将用于发送分享时使用</div>
          </el-col>
        </el-form-item>
      </el-form>
      <el-form :model="userInfo" :rules="rules" label-position="left">
        <el-form-item label="绑定手机" :label-width="labelWidth" prop="phone">
          <el-col :span="16">
            <el-input :class="{'editState': editState.phoneState == false}" v-model="userInfo.phone" :placeholder="editState.phoneState ? '未填写' : '请输入手机号码'" :disabled="editState.phoneState"></el-input>
            <el-button v-show="editState.phoneState" type="text" @click.prevent="editState.phoneState = false">去绑定</el-button>
            <el-button v-show="!editState.phoneState" size="mini" @click.prevent="edituserPhone">确定</el-button>
          </el-col>
          <el-col :span="8">
            <div class="el-form_item-tip">账户更安全</div>
          </el-col>
        </el-form-item>
      </el-form>
      <el-form :model="userInfo" label-position="left">
        <el-form-item label="身份标签" :label-width="labelWidth" prop="phone">
          <el-col :span="16">
            <div class="item-val">
              <div class="button-box">
                <button type="button" class="free">免费体验版</button>
                <button class="upgrade" type="button">升级</button>
              </div>
              <p class="use-time">有效期：2017-06-02 20:41 至 2018-06-02 20:41</p>
              <p class="warn-text">您的试用期还有 7 天，马上升级！</p>
            </div>
          </el-col>
          <el-col :span="8">
            <div class="el-form_item-tip">升级付费会员享更多特权</div>
          </el-col>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import User from 'src/server/user.js'
import { mapGetters } from 'vuex'
export default {
  data () {
    return {
      labelWidth: '100px', // label-width宽度
      editState: {
        // true为显示状态，false为编辑状态
        nameState: true,
        phoneState: true
      },
      user: {
        userName: '悟空编程', // 名称
        phone: '' // 电话
      },
      rules: {
        // 表单验证规则
        name: [
          {
            type: 'string',
            min: 0,
            max: 20,
            message: '最长20个汉字',
            trigger: 'change'
          }
        ],
        phone: [
          {
            pattern: /^((13[0-9]{1})|(14[0-9]{1})|(15[0-9]{1})|(17[0-9]{1})|(18[0-9]{1}))+\d{8}$/,
            message: '请填写正确的手机格式',
            trigger: 'change'
          }
        ]
      }
    }
  },
  computed: {
    ...mapGetters('user', {
      userInfo: 'userInfo'
    })
  },
  mounted () {
    this.userInfo.id ||
      User.self().then(res => {
        console.log(res)
        console.log(res.rs.userName)
        this.$store.commit('user/UPDATE_USERINFO', res.rs)
      })
  },
  methods: {
    edituserName () {
      // 用户名字的修改
      this.editState.nameState = true
      // 执行修改的响应api操作
      User.update({
        data: {
          userName: this.userInfo.userName
        }
      }).then(res => {
        if (res.suc) {
          this.$message({
            message: '修改成功',
            type: 'success'
          })
          this.$store.commit('user/UPDATE_USERINFO', {
            userName: this.userInfo.userName
          })
        }
      })
    },
    edituserPhone () {
      // 用户手机的修改
      this.editState.phoneState = true
      // 执行修改的响应api操作
      User.improve({
        data: {
          phone: this.userInfo.phone
        }
      }).then(res => {
        if (res.suc) {
          this.$message({
            message: '修改成功',
            type: 'success'
          })
          this.$store.commit('user/UPDATE_USERINFO', {
            phone: this.userInfo.phone
          })
        }
      })
    }
  }
}
</script>


<style lang="scss">
.box {
  border: 1px solid #d9d9d9;
  border-radius: 6px;
  background: #fff;
  margin-top: 16px;
  margin-bottom: 20px;
  padding-bottom: 166px;
  .header-box {
    text-align: center;
    margin-top: 55px;
    margin-bottom: 70px;
    img {
      width: 92px;
      height: 92px;
      border-radius: 50%;
    }
  }
}
.user-manage-wrap {
  padding: 0 50px;

  .el-form_item-tip {
    text-align: right;
    color: #979797;
  }
  .el-form-item__content {
    border-bottom: 1px solid #999;
  }
  .el-input {
    display: inline-block;
    width: initial;
  }
  .el-input__inner {
    border: 1px solid #999;
    height: 1.8em;
  }
  .el-input.is-disabled .el-input__inner {
    background: transparent !important;
    border: none;
    cursor: text;
  }

  .el-button--default {
    color: #fff;
    background-color: #fc4a46;
    border-color: #fc4a46;
  }
  .el-button--text {
    color: #fc4a46;
  }
  .item-val {
    font-size: 14px;
    position: relative;
    .user-name {
      color: #5a5a5a;
    }
    .can-operate {
      color: #fc4a46;
      cursor: pointer;
    }
    .bind-phone {
      color: #cfcfcf;
    }
    .user-name,
    .bind-phone {
      display: inline-block;
      width: 124px;
    }
    .button-box {
      button {
        height: 33px;
        border: 1px solid #d2d2d2;
        border-radius: 6px;
      }
      .free {
        width: 113px;
        background: #39af6f;
        border: 1px solid #39af6f;
        color: #fff;
      }
      .upgrade {
        width: 85px;
        color: #5a5a5a;
        background: #fff;
        margin-left: 9px;
      }
    }
    .use-time {
      font-size: 12px;
      color: #979797;
      margin-top: 13px;
    }
    .warn-text {
      background: #fff4e9;
      color: #fc4a46;
      font-size: 12px;
      display: inline-block;
      padding: 2px 6px;
      margin-top: 9px;
      line-height: 1.2;
    }
  }
}
</style>




